.m-menu {
  display: inline-block;
  font-size: 1.6rem;
}
.m-menu .menu-item {
  float: left;
  height: 6rem;
  line-height: 6rem;
  margin: 0 1.0rem;
  transition: left right 0.5s linear;
}
.m-menu .menu-item:hover {
  cursor: pointer;
}
.m-menu .menu-item.selected {
  border-bottom: 0.3rem solid green;
}



