.m-header {
  border-bottom: 0.1rem solid #e5e5e5;
  height: 6rem;
  line-height: 6rem;
  background: rgba(255, 255, 255, 0.8);
  position: fixed;
  top: 0;
  right: 0;
  bottom: auto;
  left: 0;
  z-index: initial;
}
.m-header .header-content {
  margin: 0 auto 0 auto;
  height: 6rem;
  line-height: initial;
  width: 114rem;
}
.m-header .m-logo {
  margin: 1.4rem 0rem 1.4rem 0rem;
  display: inline-block;
  width: 17.5rem;
  height: 3.2rem;
  line-height: initial;
  background: url(//wacai-file.b0.upaiyun.com/common/c/header/img/logo@2x.png) 0 0 / cover no-repeat;
}
.m-header .m-dropdown,
.m-header .m-menu {
  float: right;
}



